:root {
  --canvasColor: #c7e6e2;
  --main-color: #f4f5d9;
  --text-color-tiltle: #38bfa7;
  /* --text-color: rgb(33, 70, 54); */
  --text-color: #206466;
}

* {
  box-sizing: border-box;
  color: var(--text-color);
}

body {
  background-color: var(--canvasColor);
  background-image: url(http://static.frontendmasters.com/resources/2019-05-02-complete-intro-react-v5/image-background.png);
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.search-params {
  width: 1100px;
  margin: 0 auto;
}

.search-params form {
  background-color: var(--main-color);
  width: 360px;
  margin: 0px 25px 0px 0px;
  padding: 35px 15px 15px 15px;
  border-radius: 5px;
  float: left;
  box-shadow: 0 3px 15px rgba(51, 51, 51, 0.2);
}

.search {
  background-color: var(--main-color);
  width: 715px;
  padding: 15px;
  border-radius: 9px;
  float: left;
  margin-bottom: 25px;
  box-shadow: 0 3px 15px rgba(51, 51, 51, 0.2);
}

.details {
  width: 1100px;
  margin: 0 auto;
  background-color: var(--main-color);
  padding: 15px;
  border-radius: 9px;
  margin-bottom: 25px;
  box-shadow: 0 3px 15px rgba(51, 51, 51, 0.2);
}

.pet {
  width: 100%;
  height: 130px;
  display: block;
  overflow: hidden;
  margin: 25px 0;
  border-bottom: var(--text-color) 1px solid;
}

.pet img {
  width: 100px;
  min-height: 100px;
}

.info {
  float: left;
  width: 80%;
  height: 100px;
  padding-top: 10px;
  flex-direction: column;
  justify-content: space-around;
}

.image-container {
  clip-path: circle(50% at 50% 50%);
  width: 100px;
  height: 100px;
  float: left;
  margin: 0px 20px 0px 10px;
}

.pet h1 {
  white-space: nowrap;
  font-weight: 500;
  font-size: 1.5rem;
  color: var(--text-color);
  width: 95%;
  overflow: hidden;
  margin: 0;
  text-overflow: ellipsis;
}

header > a,
#root > div > h1 {
  display: inline-block;
  background-image: url(./images/AdoptMe.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 19rem;
  height: 11rem;
  overflow: hidden;
  text-indent: -9999px;
}

.pet h2 {
  white-space: nowrap;
  font-weight: normal;
  font-size: 1rem;
  margin: 0;
  text-overflow: ellipsis;
}

/* NAV BAR */
header a {
  text-decoration: none;
  font-weight: bold;
  display: block;
  padding-bottom: 10px;
}

header {
  margin: 0 auto;
}

.nav-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.nav-container h3 {
  font-weight: 500;
  color: #fff;
}

.header-banner {
  text-align: center;
  width: 100%;
}

/* DETIAL PAGE */

.details p {
  line-height: 1.5;
  padding: 0px 15px;
}

.details h1 {
  text-align: center;
  color: var(--text-color-tiltle);
  font-size: 60px;
  margin: 5px 0px;
}

.details h2 {
  text-align: center;
  margin: 5px 0px 20px 0px;
}

.carousel {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 400px;
  margin-top: 8px;
}

.carousel > img {
  max-width: 45%;
  max-height: 400px;
}

.carousel-smaller {
  width: 50%;
}

.carousel-smaller > img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: inline-block;
  margin: 15px;
  cursor: pointer;
  border: 2px solid #333;
}

.carousel-smaller > img.active {
  border-color: #ad343e;
  opacity: 0.6;
}

.search-params label {
  display: block;
  width: 60px;
}

.search-params input,
.search-params select {
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 600;
  height: 45px;
  width: 325px;
  padding: 8px 12px;
  border-radius: 6px;
  border-color: #c9d734;
}

.search-params button,
#modal button,
.details button {
  background-color: #ad343e;
  padding: 6px 25px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  border: #333 0px solid;
  border-radius: 5px;
  display: block;
  margin: 0 auto;
  cursor: pointer;
}

.search-params button:hover {
  background-color: #122622;
}

.search-params button:active {
  background-color: #5f1d22;
}

.search-params button:focus {
  border: 1px solid cornflowerblue;
}

#modal {
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal:empty {
  display: none;
}

#modal > div {
  background-color: white;
  max-width: 500px;
  padding: 15px;
  border-radius: 5px;
  text-align: center;
}

#modal .buttons button {
  display: inline-block;
  margin-right: 15px;
  margin: 0.5rem;
}

/* Theme Container*/
[type="radio"] {
  display: none;
}
.theme-container {
  display: flex;
}

#steelblue + label {
  background: steelblue;
}

#teal + label {
  background: teal;
}

#salmon + label {
  background: salmon;
}

#palevioletred + label {
  background: palevioletred;
}

.theme-container label {
  display: inline-block;
  width: 2.2rem;
  height: 2.2rem;
  margin: 0.75rem;
  border-radius: 50%;
  border: 0.35rem solid #fff;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1), 0 2px 4px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

[type="radio"]:checked + label {
  box-shadow: 0 0 0 2px #f9f9f9, 0 0 0 4px var(--text-color);
}

@media only screen and (max-width: 1129px) {
  header {
    margin: 0 auto;
  }

  .search-params {
    width: 95%;
  }

  .search-params form {
    margin-bottom: 20px;
  }

  .search {
    width: 55%;
    padding: 15px;
    border-radius: 9px;
    float: left;
    margin-bottom: 25px;
  }

  .details {
    width: 95%;
    margin: 0 auto;
    padding: 15px;
    border-radius: 9px;
    margin-bottom: 25px;
  }

  .info {
    width: 65%;
  }

  .info h1 {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .info h2 {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

@media only screen and (max-width: 945px) {
  .search-params {
    width: 95%;
  }

  .search {
    width: 100%;
  }
}

@media only screen and (max-width: 600px) {
  .pet {
    height: 200px;
  }

  .info {
    width: 100%;
    text-align: center;
  }

  .image-container {
    margin: 0 auto;
    float: none;
  }
  .carousel-smaller > img {
    width: 60px;
    height: 60px;
  }
}
